
.mint-toast {
  z-index: 5000;
}
.mint-toast.toast {
  z-index: 5000;
}
.mint-indicator {
  z-index: 5000;
}
#login .mint-popup-top {
  overflow: hidden;
  top: 50%;
  width: 80%;
  height: 420px;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  background: #494949;
  padding: 20px;
}
#login .mint-popup-top h3 {
    color: #fff;
    font-family: "microsoft yahei";
    margin-bottom: 20px;
    font-size: 20px;
}
#login .mint-popup-top .close_btn {
    position: absolute;
    right: 0;
    top: 0;
    color: #fff;
    background: none;
    border: 0;
}
#login .mint-popup-top .login a {
    color: #fff;
    font-family: "microsoft yahei";
    font-size: 14px;
    padding: 5px;
    display: block;
    margin-left: 20%;
}
#login .mint-popup-top .title {
    margin: 23px 0;
    color: #fff;
    background: none;
    padding-left: 10px;
}
#login .mint-popup-top .title i {
      width: 45px;
      height: 45px;
      display: block;
}
#login .mint-popup-top .title .tit {
      width: 69%;
      margin-left: 10px;
}
#login .mint-popup-top .title .tit p {
        font-family: "microsoft yahei";
        font-size: 17px;
        line-height: 23px;
}
#login .mint-popup-top .tab, #login .mint-popup-top .sub_login {
    display: block;
    width: 56%;
    height: 35px;
    line-height: 32px;
    margin: auto;
    margin-bottom: 20px;
    font-size: 16px;
    border: 0;
    background: #fff;
    font-family: "microsoft yahei";
}
#login .mint-popup-top .sub_login {
    margin-bottom: 0;
}
#login .mint-popup-top .from_box {
    margin: 20px 0;
}
#login .mint-popup-top .from_box label {
      display: block;
      width: 100%;
      color: #fff;
      position: relative;
      padding: 8px 0;
}
#login .mint-popup-top .from_box label span {
        position: absolute;
        top: 10px;
        font-size: 14px;
        margin-right: 10px;
        font-family: "microsoft yahei";
}
@media screen and (max-width: 350px) {
#login .mint-popup-top .from_box label span {
          font-size: 12px;
}
}
#login .mint-popup-top .from_box label input {
        position: relative;
        left: 50%;
        width: 56%;
        -webkit-transform: translateX(-50%);
                transform: translateX(-50%);
        box-sizing: border-box;
        padding: 5px 10px;
}
#login .mint-popup-top .register .title {
    width: 70%;
    margin: 30px auto;
}
#login .mint-popup-top .register .from_box {
    margin: 10px 0;
}
#login .mint-popup-top .register label .center_input {
    position: relative;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
}
#login .mint-popup-top .register label span {
    width: 19%;
    display: inline-block;
}
#login .mint-popup-top .register label.code span {
    float: left;
    position: relative;
    top: 0;
}
#login .mint-popup-top .register label.code input {
    width: 17%;
    left: 0;
    float: left;
    -webkit-transform: none;
            transform: none;
    box-sizing: border-box;
}
#login .mint-popup-top .register label.code .code_img {
    width: 36%;
    margin-left: 10px;
    height: 28px;
    display: inline-block;
}
#login .mint-popup-top .register label.code .code_img i {
      height: 30px;
      font-family: "microsoft yahei";
      line-height: 30px;
      font-style: normal;
      margin-left: -2px;
}
@media screen and (max-width: 350px) {
#login .mint-popup-top .register label.code .code_img i {
        font-size: 12px;
}
}
#login .mint-popup-top .register .tab {
    margin-bottom: 16px;
}
#login .mint-popup-top.heigh66 {
    height: 487px;
}
#login .layer {
  background: rgba(0, 0, 0, 0.8);
  z-index: 2012;
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  display: none;
}
#login .layer.layer_show {
    display: block;
}
#login .find_box {
  position: fixed;
  left: 10%;
  width: 80%;
  box-sizing: border-box;
  height: 400px;
  margin-top: -200px;
  padding: 20px;
  background: #000;
  z-index: 20002;
  top: -50%;
  transition: all .5s;
}
#login .find_box h1 {
    color: #fff;
    font-size: 25px;
    font-weight: 500;
}
#login .find_box p {
    color: #fff;
    font-size: 14px;
    margin-top: 20px;
}
#login .find_box label {
    padding: 20px 0;
    display: block;
}
#login .find_box label span {
      color: #fff;
      font-family: "microsoft yahei";
      font-size: 14px;
}
#login .find_box label input {
      padding: 5px 10px;
      margin-top: 10px;
}
#login .find_box button {
    display: block;
    font-size: 14px;
    font-family: "din",'micrsoft yahei';
    margin: auto;
    margin-top: 30px;
    border: 1px #fff solid;
    padding: 3px 15px;
    color: #fff;
}
#login .find_box.find_show {
    top: 50%;
}



