
#zh_project .banner {
  height: 135px;
  overflow: hidden;
}
#zh_project .banner img {
    width: 100%;
}
#zh_project .title {
  height: 40px;
  line-height: 40px;
  font-size: 18px;
  color: #020202;
  font-weight: bolder;
  background: #d3d3d3;
  padding: 0 20px;
}
#zh_project h4 {
  line-height: 35px;
  font-size: 18px;
  font-weight: bolder;
}
#zh_project p {
  padding: 10px 0;
  line-height: 23px;
  font-size: 17px;
  color: #3c3c3c;
  font-family: "microsoft yahei";
  font-weight: 400;
}
#zh_project .content {
  padding: 40px 20px;
}
#zh_project .content .project_main {
    padding: 10px 0;
}
#zh_project .content .project_main .j_box span {
      font-weight: 600;
}
#zh_project .content .project_main .j_box img {
      max-width: 100%;
      display: block;
      margin: 10px auto;
}



