
.mint-indicator-wrapper {
  z-index: 5000;
}
.mint-toast {
  z-index: 9999;
}
#pc_header .mian_1000px {
  width: 1000px;
  margin: auto;
}
#pc_header .mint-popup-top {
  background: #000;
  top: 50%;
  width: 300px;
  height: 300px;
  margin-top: -150px;
}
#pc_header .mint-popup-top .close_btn {
    position: absolute;
    right: 0;
    top: 0;
    color: #fff;
    cursor: pointer;
}
#pc_header .mint-popup-top .imgbox {
    width: 86%;
    margin: auto;
    margin-top: 50px;
}
#pc_header .mint-popup-top .imgbox img {
      width: 60%;
      margin: auto;
      display: block;
}
#pc_header .mint-popup-top .imgbox h4 {
      margin-top: 3%;
      text-align: center;
      font-weight: normal;
      font-size: 14px;
      color: #fff;
}
#pc_header .mint-popup-top .imgbox:last-child {
      margin-top: 50px;
}
#pc_header nav {
  min-height: 61px;
  background: #363533;
  position: relative;
  z-index: 8000;
  min-width: 1000px;
}
#pc_header nav .logo {
    position: absolute;
    width: 130px;
    height: 130px;
    z-index: 200;
    cursor: pointer;
}
#pc_header nav .logo img {
      width: 100%;
      height: 100%;
}
#pc_header nav .user {
    float: right;
    line-height: 60px;
}
#pc_header nav .user a button {
      width: 155px;
}
#pc_header nav .user a button .username {
        position: relative;
        display: inline-block;
        width: 65px;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
        top: 4px;
        padding-right: 5px;
        text-align: right;
}
#pc_header nav .user button {
      color: #999;
      font-size: 16px;
      transition: all .3s;
      cursor: pointer;
      width: 50px;
}
#pc_header nav .user button:hover {
        color: #fff;
}
#pc_header nav .nav_list {
    position: relative;
    margin-left: 130px;
    background: #fff;
    padding-left: 0;
    padding-left: 10px;
}
#pc_header nav .nav_list li {
      cursor: pointer;
      line-height: 60px;
      float: left;
      color: #fff;
      font-weight: 400;
      text-align: center;
      font-size: 16px;
      font-weight: 400;
      color: #efefef;
      padding: 0 13.5px;
      transition: all .3s;
}
#pc_header nav .nav_list li:hover {
        background: #232323;
}
#pc_header nav .nav_list li.current {
        background: #232323;
}
#pc_header nav .list2_box {
    position: absolute;
    height: 250px;
    background: #232323;
    width: 100%;
    opacity: 0;
    transition: all .2s;
    top: -500px;
    min-width: 1000px;
}
#pc_header nav .list2_box.show {
      display: block;
      opacity: 1;
      top: 59px;
}
#pc_header nav .list2_box .nav_list2 {
      margin-top: 10px;
      margin-left: 115px;
      float: left;
}
#pc_header nav .list2_box .nav_list2 li {
        cursor: pointer;
        color: #fff;
        line-height: 30px;
        font-size: 14px;
}
#pc_header nav .list2_box .nav_list2 li a {
          color: #fff;
}
#pc_header nav .list2_box .img_box {
      float: right;
      height: 250px;
}
#pc_header nav .list2_box .img_box img {
        height: 100%;
}



