
@charset "UTF-8";
#formUped form .user_body {
  padding-top: 20px;
  border-top: 1px #d3d3d3 solid;
}
#formUped form .user_body p {
    margin-top: 20px;
    color: #030303;
    font-size: 14px;
    line-height: 14px;
}
#formUped form .user_body input {
    border-radius: 5px;
    ime-mode: disabled;
}
#formUped form .user_body .mint-radiolist-title {
    font-size: 14px;
    font-weight: bolder;
    color: #000;
}
#formUped form .user_body .mint-radiolist-title:before {
      content: '· ';
      font-size: 16px;
      font-weight: bolder;
}
#formUped form .user_body .mint-radiolist-label span {
    font-size: 12px;
}
#formUped form .user_body .mint-cell-wrapper {
    height: 30px;
}
#formUped form .user_body .main h3 {
    font-size: 13px;
    color: #c00;
    font-weight: normal;
}
#formUped form .user_body .main label {
    margin: 20px 0;
}
#formUped form .user_body .main label span {
      font-size: 14px;
      font-weight: bolder;
      color: #000;
}
#formUped form .user_body .main label span:before {
        content: '· ';
        font-size: 16px;
        font-weight: bolder;
}
#formUped form .user_body .main label textarea {
      margin: 10px 0;
      border-radius: 5px;
      width: 100%;
      min-height: 100px;
}
#formUped form .user_body .main label input {
      display: block;
      margin: 10px 0;
      padding: 5px 10px;
      margin-left: 10px;
      ime-mode: disabled;
}
#formUped form .user_body .daoshi label.input span {
    font-weight: bolder;
    display: inline-block;
    font-size: 13px;
    padding-left: 10px;
}
#formUped form .user_body .daoshi label.input input {
    display: inline-block;
    margin: 10px 0;
    padding: 5px 10px;
    margin-left: 10px;
}
#formUped form .user_body .daoshi .project a {
    display: inline-block;
    width: auto;
}
#formUped form button {
  height: 35px;
  width: 100px;
  background: #000;
  border: 0;
  color: #fff;
  display: block;
  margin: auto;
  margin-top: 20px;
}



