
* {
  margin: 0;
}
.mint-toast {
  z-index: 9999;
}
.toast {
  z-index: 9999;
}
.pc_hide {
  display: none !important;
}
.mobile_hide {
  display: block !important;
}
@media screen and (max-width: 1000px) {
.pc_hide {
    display: block !important;
}
.mobile_hide {
    display: none !important;
}
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #171717;
}
#app .toast {
    z-index: 9999;
}
#app .clear:after {
    content: "";
    display: block;
    clear: both;
}
#app .l {
    float: left;
}
#app .r {
    float: right;
}
#app ul {
    list-style: none;
}
#app input, #app textarea, #app button {
    outline: none;
    border-radius: 0;
    resize: none;
}
#app input:focus, #app textarea:focus, #app button:focus {
      border-color: #802358;
}
#app select {
    outline: none;
    border: none;
}
#app .icon {
    width: 1em;
    height: 1em;
    vertical-align: -0.15em;
    fill: currentColor;
    overflow: hidden;
}
#app .iconfont {
    width: 1em;
    height: 1em;
    font-size: 1.2rem;
    vertical-align: -0.15em;
}
#app a {
    text-decoration: none;
}
#app h1, #app h2, #app h3, #app h4, #app h5, #app code, #app a {
    font-family: "din", "microsoft yahei", sans-serif !important;
    margin: 0;
    padding: 0;
}
#app li {
    font-family: "din", "microsoft yahei", sans-serif !important;
}
#app p {
    font-family: "din", "microsoft yahei", sans-serif !important;
}
#app div {
    font-family: "din", "microsoft yahei", sans-serif !important;
}
@media screen and (min-width: 1000px) {
#app p {
      color: #777;
}
}
#app button {
    background: transparent;
    border: 0;
    cursor: pointer;
}



