
#order .c_r {
  color: #c11010;
}
#order .order_box .list1 {
  width: 100%;
  box-sizing: border-box;
  padding-left: 0;
  height: auto;
  border: 1px #d3d3d3 solid;
  margin-bottom: 20px;
}
#order .order_box .list1 li {
    border-bottom: 1px  #d3d3d3 solid;
    padding: 10px;
    font-size: 14px;
}
#order .order_box .list1 li:last-child {
      border: 0;
}
#order .order_box .good {
  padding-left: 0;
}
#order .order_box .good li {
    height: 100px;
    border: 1px  #d3d3d3 solid;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    margin-bottom: 20px;
}
#order .order_box .good li .good_img {
      width: 100px;
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      border-right: 1px #d3d3d3 solid;
      -webkit-box-align: center;
          -ms-flex-align: center;
              align-items: center;
      -webkit-box-pack: center;
          -ms-flex-pack: center;
              justify-content: center;
}
#order .order_box .good li .good_img img {
        max-width: 100%;
        max-height: 100%;
}
#order .order_box .good li .good_disc {
      box-sizing: border-box;
      position: relative;
      padding: 5px;
      font-size: 12px;
      -webkit-box-flex: 1;
          -ms-flex: 1;
              flex: 1;
}
#order .order_box .good li .good_disc h4 {
        position: absolute;
        bottom: 5px;
        width: 95%;
}
#order .order_box .good li .good_disc h4 span {
          float: right;
}
#order .order_box button.delete {
  background: transparent;
  border: 0;
  margin-top: 10px;
}



