
#business .maodian {
  position: relative;
  top: -50px;
}
#business .banner {
  height: 80px;
  overflow: hidden;
}
#business .banner img {
    width: 100%;
}
#business .title {
  height: 40px;
  line-height: 40px;
  font-size: 18px;
  color: #020202;
  font-weight: bolder;
  background: #d3d3d3;
  padding: 0 20px;
}
#business .content {
  background: #fff;
  padding: 40px 20px;
}
#business .box_head {
  padding: 20px 0;
  border-bottom: 1px #949393 solid;
}
#business .box_head span {
    display: block;
    color: #d3be95;
    margin-right: 10px;
    font-size: 25px;
    transition: all .5s;
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
}
#business .box_head span.tran {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
}
#business .box_head h2 {
    width: 80%;
    color: #949393;
}
#business .box_main {
  opacity: 0;
  height: 0;
  overflow: hidden;
  transition: opacity 1s;
}
#business .box_main p {
    padding: 10px 0;
    line-height: 23px;
    font-size: 17px;
    color: #3c3c3c;
    font-family: "microsoft yahei";
    font-weight: 400;
}
#business .box_main.show {
    opacity: 1;
    height: auto;
}



