
@charset "UTF-8";
#home-index {
  /*
			list
		*/
  /*
		*/
}
#home-index .pc_banner.mint-tab-container {
    height: 450px;
    width: 100%;
    left: 0;
}
#home-index .pc_banner.mint-tab-container .mint-tab-container-wrap {
      height: 100%;
}
#home-index .pc_banner.mint-tab-container .mint-tab-container-wrap .mint-tab-container-item {
        height: 100%;
}
#home-index .pc_banner.mint-tab-container .mint-tab-container-wrap .mint-tab-container-item .tab_list {
          height: 100%;
          display: -webkit-box;
          display: -ms-flexbox;
          display: flex;
          -webkit-box-pack: center;
              -ms-flex-pack: center;
                  justify-content: center;
          -ms-flex-line-pack: center;
              align-content: center;
}
#home-index .pc_banner.mint-tab-container .mint-tab-container-wrap .mint-tab-container-item .tab_list img {
            height: 100%;
            width: auto;
}
#home-index .pc_banner.mint-tab-container .index {
      position: absolute;
      bottom: 55px;
      -webkit-transform: translateX(-50%);
              transform: translateX(-50%);
      left: 50%;
      color: #fff;
}
#home-index .pc_banner.mint-tab-container .index .index_yuan {
        cursor: pointer;
        box-sizing: border-box;
        transition: all .3s;
        background: rgba(202, 202, 202, 0.6);
        border-radius: 50%;
        overflow: hidden;
        width: 10px;
        margin: 5px;
        height: 10px;
        display: inline-block;
}
#home-index .pc_banner.mint-tab-container .index .index_yuan.on {
          background: #ffa838;
          opacity: 0.7;
}
#home-index #swipe {
    height: 170px;
}
#home-index .mint-swipe img {
    height: 170px;
    position: absolute;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
}
#home-index .mint-swipe a {
    display: block;
    width: 100%;
    height: auto;
}
#home-index .mint-swipe .mint-swipe-item {
    height: 100%;
    overflow: hidden;
}
#home-index .mint-swipe .mint-swipe-indicator {
    opacity: 0.4;
}
#home-index .mint-swipe .mint-swipe-indicator.is-active {
    opacity: 1;
}
#home-index .mint-swipe .mint-swipe-indicators {
    bottom: 3px;
}
@media screen and (max-width: 345px) {
#home-index #swipe {
      height: 145px;
}
#home-index .mint-swipe img {
      height: 145px;
}
}
#home-index .list_box {
    background: #e8e8e8;
}
#home-index .list_box .notice_list {
      padding: 20px;
}
#home-index .list_box .notice_list li {
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        /*设为伸缩容器*/
        -webkit-box-orient: horizontal;
        -webkit-box-direction: normal;
            -ms-flex-flow: row;
                flex-flow: row;
        /*伸缩项目单行排列*/
        height: 110px;
        background: #fff;
        margin-bottom: 20px;
}
#home-index .list_box .notice_list li .img_box {
          width: 110px;
          height: 110px;
}
#home-index .list_box .notice_list li .img_box img {
            width: 100%;
            height: 100%;
}
#home-index .list_box .notice_list li .content_box {
          display: -webkit-box;
          display: -ms-flexbox;
          display: flex;
          -webkit-box-flex: 1;
              -ms-flex: 1;
                  flex: 1;
          -webkit-box-align: center;
              -ms-flex-align: center;
                  align-items: center;
          padding: 20px;
          vertical-align: middle;
}
#home-index .list_box .notice_list li .content_box h3 {
            display: none;
}
#home-index .list_box .notice_list li:last-child {
        margin: 0;
}
#home-index .tab_box {
    height: 120px;
    position: relative;
    overflow: hidden;
}
#home-index .tab_box .tab_list {
      width: 90%;
      padding: 0;
      height: auto;
      margin: auto;
      margin-top: 10px;
}
#home-index .tab_box button {
      position: absolute;
      background: none;
      height: 70px;
      top: 50%;
      -webkit-transform: translateY(-50%);
              transform: translateY(-50%);
      z-index: 20;
      border: 0;
}
#home-index .tab_box button i {
        display: block;
        font-size: 40px;
        color: #e3e3e3;
        font-weight: normal;
}
#home-index .tab_box button:nth-child(1) {
        left: -10px;
}
#home-index .tab_box button:nth-child(2) {
        right: -10px;
}
#home-index .tab_box h5 {
      font-size: 14px;
      font-weight: normal;
      font-style: normal;
      text-align: center;
      margin-top: 25px;
      margin-bottom: 10px;
}
@media screen and (min-width: 1000px) {
#home-index #swipe {
      height: 450px;
}
#home-index .mint-tab-container {
      width: 95%;
      left: 2.5%;
      margin-top: 0px;
}
#home-index .mint-swipe img {
      position: absolute;
      left: 50%;
      -webkit-transform: translateX(-50%);
              transform: translateX(-50%);
      height: 450px;
      width: auto;
      display: block;
      margin: auto;
}
#home-index .mint-swipe .mint-swipe-indicator {
      background: #8a8787;
}
#home-index .mint-swipe .mint-swipe-indicator.is-active {
        background: #fff;
}
#home-index .mint-swipe .mint-swipe-indicators {
      bottom: 60px;
}
#home-index .list_box {
      height: 110px;
      width: 100%;
      position: relative;
}
#home-index .list_box .notice_list {
        position: absolute;
        top: -65px;
        width: 1000px;
        margin: auto;
        left: 50%;
        -webkit-transform: translateX(-50%);
                transform: translateX(-50%);
}
#home-index .list_box .notice_list li {
          width: 31.3%;
          float: left;
          height: 130px;
          margin-right: 3%;
}
#home-index .list_box .notice_list li:last-child {
            margin-right: 0;
}
#home-index .list_box .notice_list li a {
            height: 130px;
}
#home-index .list_box .notice_list li .img_box {
            width: 130px;
            height: 130px;
}
#home-index .list_box .notice_list li .content_box {
            height: 110px;
            display: block;
            padding: 10px;
}
#home-index .list_box .notice_list li .content_box h4 {
              display: none;
}
#home-index .list_box .notice_list li .content_box h3 {
              margin-top: 5px;
              display: block;
              font-size: 13px;
              font-weight: normal;
              color: #969696;
}
#home-index .list_box .notice_list li .content_box h3:nth-child(2):hover {
                color: #333;
}
#home-index .tab_box {
      width: 1000px;
      margin: auto;
      height: 250px;
}
}



