
#newyork .banner {
  height: 84px;
  overflow: hidden;
}
#newyork .title {
  height: 40px;
  line-height: 40px;
  font-size: 18px;
  color: #020202;
  font-weight: bolder;
  background: #d3d3d3;
  padding: 0 20px;
}
#newyork .content {
  padding: 40px 20px;
}
#newyork .content h4 {
    line-height: 35px;
    font-size: 17px;
    font-weight: bolder;
}
#newyork .content p {
    padding: 10px 0;
    line-height: 23px;
    font-size: 15px;
    color: #3c3c3c;
    font-family: "microsoft yahei";
    font-weight: 400;
}
#newyork .content p a {
      color: #000;
}
#newyork .content .new_box {
    padding: 20px 0;
}
#newyork .content .new_box .new_head {
      padding: 15px 0;
      border-bottom: 1px #949393 solid;
}
#newyork .content .new_box .new_head span {
        display: block;
        color: #d3be95;
        margin-right: 10px;
        font-size: 25px;
        -webkit-transform: rotate(180deg);
                transform: rotate(180deg);
        transition: all .5s;
}
#newyork .content .new_box .new_head span.tran {
          -webkit-transform: rotate(0deg);
                  transform: rotate(0deg);
}
#newyork .content .new_box .new_head h2 {
        width: 80%;
        font-size: 20px;
        color: #949393;
}
#newyork .content .new_box .body {
      height: 0;
      opacity: 0;
      overflow: hidden;
      transition: all 1s;
}
#newyork .content .new_box .body.show {
        height: auto;
        opacity: 1;
}



