
#zxjsc-footer {
  padding: 18px;
  background: #363532;
  height: 150px;
  box-sizing: border-box;
}
#zxjsc-footer .footer_main {
    border-top: 1px #999 dashed;
    color: #999;
}
#zxjsc-footer .footer_main .logo_box {
      width: 206px;
      display: block;
      margin: auto;
      margin-top: 23px;
}
#zxjsc-footer .footer_main .logo_box img {
        width: 90px;
}
#zxjsc-footer .footer_main .logo_box img:nth-child(2) {
          margin-top: 5px;
}
#zxjsc-footer .footer_main p img {
      width: 90%;
      display: block;
      margin: 15px auto 0 auto;
      max-width: 320px;
}
#zxjsc-footer .footer_main h5 {
      text-align: center;
      margin-top: 20px;
}
#zxjsc-footer .footer_main h5 a {
        color: #999;
        display: inline-block;
        margin: 0 20px;
        text-decoration: none;
        font-style: normal;
}



