
#userlogo .banner {
  height: auto;
  overflow: hidden;
}
#userlogo .banner img {
    position: relative;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    height: 140px;
}
#userlogo .title {
  height: 40px;
  line-height: 40px;
  font-size: 18px;
  color: #020202;
  font-weight: bolder;
  background: #d3d3d3;
  padding: 0 20px;
}
@media screen and (max-width: 1000px) {
#userlogo .title {
    margin-top: -6px;
}
}
#userlogo .content {
  padding: 20px;
}
#userlogo .content .img_box {
    height: 100px;
    width: 100px;
    border: 1px #d3d3d3 solid;
    padding: 3px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -ms-flex-line-pack: center;
        align-content: center;
}
#userlogo .content .img_box img {
      max-height: 100%;
      width: auto;
      height: auto;
      max-width: 100%;
}
#userlogo .content .disc {
    margin-left: 20px;
}
#userlogo .content .disc h4 {
      font-size: 16px;
      line-height: 26px;
      font-weight: bloder;
      color: #000;
}
#userlogo .content .disc h5 {
      font-weight: normal;
      font-size: 14px;
      color: #000;
      line-height: 25px;
}
#userlogo .content .disc h5 a {
        font-weight: 600;
        display: inline-block;
        color: #000;
        margin: 0 4px;
}
#userlogo .content .disc h5 a:first-child {
          margin-left: 0;
}
#userlogo .content .con_box {
    margin-top: 20px;
    border-top: 1px #d3d3d3 solid;
}
#userlogo .content .con_box label {
      position: relative;
      cursor: pointer;
      margin: 10px 0;
      display: block;
      cursor: pointer;
}
#userlogo .content .con_box label span {
        width: 30%;
        text-align: right;
        display: inline-block;
        font-family: "microsoft yahei";
}
#userlogo .content .con_box label input {
        width: 50%;
        padding: 5px 10px;
        cursor: pointer;
        opacity: 0;
}
#userlogo .content .con_box label .xianshi {
        cursor: pointer;
        height: 40px;
        width: 60%;
        position: absolute;
        left: 35%;
        top: 8px;
        font-size: 13px;
}
#userlogo .content .con_box p {
      padding: 10px 0;
      font-size: 15px;
}
@media screen and (min-width: 1000px) {
#userlogo .content .con_box p {
        padding-left: 140px;
}
}
#userlogo .content .con_box button {
      width: 40%;
      height: 30px;
      line-height: 28px;
      font-size: 15px;
      border: 0;
      margin: auto;
      margin-top: 10px;
      display: block;
      background: #d3d3d3;
      color: #000;
      text-align: center;
      font-family: "microsoft yahei";
}
@media screen and (min-width: 1000px) {
#userlogo .title1000 {
    width: 1000px;
    margin: auto;
}
#userlogo .banner {
    height: 249px;
}
#userlogo .banner img {
      height: 100%;
}
#userlogo .content {
    position: relative;
    width: 710px;
    padding-right: 290px;
    margin: auto;
    padding-left: 0;
}
#userlogo .content .disc {
      width: 202px;
}
#userlogo .content .disc h5 a {
        float: none;
}
}
@media screen and (max-width: 358px) {
#userlogo .title {
    font-size: 16px;
    margin-top: -8px;
    height: 40px;
}
#userlogo .content .con_box p {
    font-size: 13px;
}
#userlogo .content .img_box {
    width: 80px;
    height: 80px;
}
#userlogo .content .disc {
    margin-left: 10px;
}
#userlogo .content .disc h4 {
      font-size: 14px;
}
#userlogo .content .disc h5 {
      font-size: 12px;
}
#userlogo .content .mine_list {
    margin-top: 13px;
}
#userlogo .content .mine_list a {
      font-size: 13px;
      padding: 5px 4%;
}
}



