
@charset "UTF-8";
#zxjsc-header {
  /*菜单*/
}
#zxjsc-header header {
    background: #363532;
    height: 45px;
}
#zxjsc-header header .logo {
      margin-left: 20px;
      float: left;
      height: 45px;
}
#zxjsc-header header .menu {
      margin-right: 20px;
      height: 45px;
}
#zxjsc-header header .user {
      margin-right: 20px;
}
#zxjsc-header header .user img {
        display: block;
        margin: auto;
}
#zxjsc-header header img {
      height: 45px;
}
#zxjsc-header .menu_list {
    margin-top: 45px;
    width: 100%;
    padding: 0 20px;
    box-sizing: border-box;
    padding-bottom: 30px;
}
#zxjsc-header .menu_list li {
      color: #000;
      font-family: "microsoft yahei";
      font-weight: bolder;
      line-height: 50px;
      font-size: 14px;
      border-top: 1px #BCBCBC solid;
}
#zxjsc-header .menu_list li span {
        font-size: 44px;
        color: #BCBCBC;
        line-height: 42px;
        font-weight: 500;
}
#zxjsc-header .menu_list li span .pass {
          position: relative;
          right: 0;
          width: 30px;
          height: 30px;
          top: 5px;
}
#zxjsc-header .menu_list li span .pass i {
            position: absolute;
            width: 25px;
            height: 3px;
            top: 15px;
            display: block;
            background: #BCBCBC;
}
#zxjsc-header .menu_list li span .pass i:last-child {
              -webkit-transform: rotate(90deg);
                      transform: rotate(90deg);
}
#zxjsc-header .menu_list li span .pass.heng i:last-child {
            -webkit-transform: rotate(0deg);
                    transform: rotate(0deg);
}
#zxjsc-header .menu_list li .menu_list_2 {
        transition: all .3s;
        width: 80%;
        margin-left: 20px;
        height: 0;
        overflow: hidden;
}
#zxjsc-header .menu_list li .menu_list_2 li {
          border: 0;
          color: #0E0E0E;
          font-weight: normal;
          font-size: 13px;
          line-height: 40px;
}
#zxjsc-header .menu_list li .menu_list_2 li a {
            display: block;
            width: 100%;
            color: #0E0E0E;
            height: 100%;
            font-size: 13px;
}
#zxjsc-header .menu_list li .menu_list_2 li.hui a {
            color: #b3b3b3;
}
#zxjsc-header .mint-popup-top {
    top: 45px;
    overflow: auto;
    height: 420px;
}
#zxjsc-header .v-modal {
    top: 45px;
}
#zxjsc-header .mint-popup-top {
    width: 100%;
}
#zxjsc-header .mint-popup-bottom {
    height: 60%;
    top: 20%;
    width: 80%;
}
#zxjsc-header .second {
    position: fixed;
    width: 100%;
    top: 0;
    height: 100%;
    z-index: 500;
}
#zxjsc-header .second .close_btn {
      position: absolute;
      right: 0;
      top: 0;
      color: #fff;
      background: none;
      border: 0;
}
#zxjsc-header .second .layer {
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      background: rgba(0, 0, 0, 0.5);
}
#zxjsc-header .second .content_box {
      position: absolute;
      width: 60%;
      left: 20%;
      padding-top: 50px;
      top: 50%;
      -webkit-transform: translateY(-50%);
              transform: translateY(-50%);
      height: 40%;
      background: #000;
}
#zxjsc-header .second .content_box .imgbox {
        width: 86%;
        margin: auto;
        margin-top: 15%;
}
#zxjsc-header .second .content_box .imgbox img {
          width: 60%;
          margin: auto;
          display: block;
}
#zxjsc-header .second .content_box .imgbox h4 {
          margin-top: 3%;
          text-align: center;
          font-weight: normal;
          font-size: 14px;
          color: #fff;
}
#zxjsc-header .second .content_box .imgbox:last-child {
          margin-top: 10%;
}



