
#password .banner {
  height: auto;
  overflow: hidden;
}
#password .banner img {
    position: relative;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    height: 140px;
}
#password .title {
  height: 40px;
  line-height: 40px;
  font-size: 18px;
  color: #020202;
  font-weight: bolder;
  background: #d3d3d3;
  padding: 0 20px;
}
@media screen and (max-width: 1000px) {
#password .title {
    margin-top: -6px;
}
}
#password .content {
  padding: 20px;
}
#password .content .img_box {
    height: 100px;
    width: 100px;
    border: 1px #d3d3d3 solid;
    padding: 3px;
}
#password .content .img_box img {
      width: 100%;
      height: 100%;
}
#password .content .disc {
    margin-left: 20px;
}
#password .content .disc h4 {
      font-size: 16px;
      line-height: 26px;
      font-weight: bloder;
      color: #000;
}
#password .content .disc h5 {
      font-weight: normal;
      font-size: 14px;
      color: #000;
      line-height: 25px;
}
#password .content .disc h5 a {
        font-weight: 600;
        display: inline-block;
        color: #000;
        margin: 0 4px;
}
#password .content .disc h5 a:first-child {
          margin-left: 0;
}
#password .content .form_box {
    margin-top: 20px;
    border-top: 1px #d3d3d3 solid;
}
#password .content .form_box label {
      padding: 20px 0;
      display: block;
}
#password .content .form_box label span {
        width: 30%;
        text-align: right;
        font-size: 14px;
        display: inline-block;
        font-family: "microsoft yahei";
}
#password .content .form_box label input {
        padding: 5px 10px;
}
#password .content .form_box button {
      width: 40%;
      height: 30px;
      line-height: 28px;
      font-size: 15px;
      border: 0;
      margin: auto;
      margin-top: 10px;
      display: block;
      background: #d3d3d3;
      color: #000;
      text-align: center;
      font-family: "microsoft yahei";
}
@media screen and (min-width: 1000px) {
#password .title1000 {
    width: 1000px;
    margin: auto;
}
#password .banner {
    height: 249px;
}
#password .banner img {
      height: 100%;
}
#password .content {
    position: relative;
    width: 710px;
    padding-right: 290px;
    margin: auto;
    padding-left: 0;
}
#password .content .disc {
      width: 202px;
}
#password .content .disc h5 a {
        float: none;
}
}
@media screen and (max-width: 358px) {
#password .title {
    font-size: 16px;
    margin-top: -8px;
    height: 40px;
}
#password .content .img_box {
    width: 90px;
    height: 90px;
}
#password .content .disc {
    margin-left: 10px;
}
#password .content .disc h4 {
      font-size: 14px;
}
#password .content .disc h5 {
      font-size: 12px;
}
#password .content .mine_list {
    margin-top: 13px;
}
#password .content .mine_list a {
      font-size: 13px;
      padding: 5px 4%;
}
}



