
#teaonline .banner {
  width: auto;
  overflow: hidden;
}
#teaonline .banner img {
    width: 100%;
}
#teaonline .title {
  margin-top: -5px;
  height: 40px;
  line-height: 40px;
  font-size: 18px;
  color: #020202;
  font-weight: bolder;
  background: #d3d3d3;
  padding: 0 20px;
}
#teaonline .content {
  padding: 40px 20px;
}
#teaonline .content h4 {
    line-height: 35px;
    font-size: 16px;
    font-weight: bolder;
}
#teaonline .content p {
    padding: 10px 0;
    line-height: 23px;
    font-size: 15px;
    color: #3c3c3c;
    font-family: "microsoft yahei";
    font-weight: 400;
}
#teaonline .content label h5 {
    line-height: 30px;
    font-size: 15px;
    margin: 10px 0;
}
#teaonline .content label input {
    padding: 5 10px;
    height: 30px;
    box-sizing: border-box;
    line-height: 30px;
    width: 60%;
}
#teaonline .content label i {
    font-size: 30px;
    margin-left: 10px;
}
#teaonline .content label textarea {
    width: 100%;
    min-height: 90px;
}
#teaonline .content label.recommended {
    padding: 10px 0;
    display: block;
}
#teaonline .content label.recommended span {
      font-family: "microsoft yahei";
      font-size: 15px;
}
#teaonline .content label.recommended input {
      width: 50%;
}
#teaonline .content label.recommended i {
      font-size: 14px;
      font-family: "microsoft yahei";
}
#teaonline .content .mint-button--default {
    background: #000;
    color: #fff;
    padding: 0 30px;
    font-size: 15px;
    display: block;
    margin: auto;
    font-family: "microsoft yahei";
    margin-top: 30px;
}
@media screen and (min-width: 1000px) {
#teaonline .title1000 {
    width: 1000px;
    margin: auto;
}
#teaonline .content {
    width: 1000px;
    box-sizing: border-box;
    padding-right: 290px;
    margin: auto;
}
#teaonline .content label input {
      width: 160px;
}
#teaonline .content label.recommended input {
      width: 160px;
}
}



