
#zh_award .banner {
  height: 135px;
  overflow: hidden;
}
#zh_award .banner img {
    width: 100%;
}
#zh_award .title {
  height: 40px;
  line-height: 40px;
  font-size: 18px;
  color: #020202;
  font-weight: bolder;
  background: #d3d3d3;
  padding: 0 20px;
}
#zh_award .content {
  padding: 40px 20px;
}
#zh_award .content h4 {
    line-height: 35px;
    font-size: 17px;
    font-weight: bolder;
}
#zh_award .content p {
    padding: 10px 0 20px 0;
    line-height: 23px;
    font-size: 17px;
    color: #3c3c3c;
    font-family: "microsoft yahei";
    font-weight: 400;
}
#zh_award .content .main2 .main2_head {
    padding: 20px 0;
    border-bottom: 1px #949393 solid;
}
#zh_award .content .main2 .main2_head span {
      display: block;
      color: #d3be95;
      margin-right: 10px;
      font-size: 25px;
      -webkit-transform: rotate(180deg);
              transform: rotate(180deg);
}
#zh_award .content .main2 .main2_head h2 {
      width: 80%;
      color: #949393;
}
#zh_award .content .main2 .body {
    height: 0;
    overflow: hidden;
    opacity: 0;
    transition: all 1s;
}
#zh_award .content .main2 .body.active {
      height: auto;
      opacity: 1;
}



