
#awards .banner {
  height: auto;
  overflow: hidden;
}
#awards .banner img {
    position: relative;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    height: 140px;
}
#awards .title {
  margin-top: 0px;
  height: 40px;
  line-height: 40px;
  font-size: 18px;
  color: #020202;
  font-weight: bolder;
  background: #d3d3d3;
  padding: 0 20px;
}
@media screen and (max-width: 1000px) {
#awards .title {
    margin-top: -6px;
}
}
#awards .content {
  padding: 20px;
}
#awards .content h4 {
    margin: 8px 0;
}
#awards .content p {
    font-size: 14px;
    font-family: "microsoft yahei";
    line-height: 23px;
}
#awards .content p:nth-child(1) {
      padding: 0;
}
#awards .content p span {
      font-weight: bolder;
}
@media screen and (min-width: 1000px) {
#awards .title1000 {
    width: 1000px;
    margin: auto;
}
#awards .banner {
    height: 249px;
}
#awards .banner img {
      width: auto;
      height: 100%;
}
#awards .content {
    width: 1000px;
    margin: auto;
    padding-left: 0;
    box-sizing: border-box;
    padding-right: 290px;
}
}



