
#party .banner {
  height: 80px;
  overflow: hidden;
}
#party .banner img {
    width: 100%;
}
#party .title {
  height: 40px;
  line-height: 40px;
  font-size: 18px;
  color: #020202;
  font-weight: bolder;
  background: #d3d3d3;
  padding: 0 20px;
}
#party .content {
  padding: 40px 20px;
}
#party .content h4 {
    line-height: 35px;
    font-size: 19px;
    font-weight: bolder;
}
#party .content p {
    padding: 10px 0;
    line-height: 23px;
    font-size: 15px;
    color: #3c3c3c;
    font-family: "microsoft yahei";
    font-weight: 400;
}



