
@charset "UTF-8";
#about_box .banner {
  height: 80px;
  overflow: hidden;
}
#about_box .banner img {
    width: 100%;
}
#about_box .title {
  height: 40px;
  line-height: 40px;
  font-size: 18px;
  color: #020202;
  font-weight: bolder;
  background: #d3d3d3;
  padding: 0 20px;
}
#about_box .content {
  background: #fff;
  padding: 40px 20px;
}
#about_box .content .card img {
    width: 130px;
    height: 130px;
    border: 1px #d3d3d3 solid;
}
#about_box .content .card .disc {
    margin-left: 20px;
}
#about_box .content .card .disc h3 {
      margin-top: 51px;
      line-height: 30px;
      font-size: 18px;
      font-weight: 700;
      color: #3c3c3c;
}
#about_box .content .card .disc h4 {
      line-height: 30px;
      font-size: 15px;
      color: #3c3c3c;
}
#about_box .content .card .disc p {
      height: 30px;
      line-height: 22px;
}
#about_box .content .card .disc p img {
        width: 20px;
        margin-right: 10px;
        height: auto;
        border: 0;
}
#about_box .content .card .disc p span {
        margin-right: 10px;
}
#about_box .content .main h3 {
    line-height: 45px;
    font-weight: 800;
    color: #3c3c3c;
    font-family: "微软雅黑";
}
#about_box .content .main p {
    padding: 10px 0;
    line-height: 23px;
    font-size: 17px;
    color: #3c3c3c;
    font-family: "microsoft yahei";
    font-weight: 400;
}
#about_box .content .qian {
    padding: 30px 0;
}
#about_box .about_head {
  max-width: 90%;
  display: block;
  margin: auto;
}



