
@charset "UTF-8";
#memebersign .banner {
  width: 100%;
  overflow: hidden;
}
#memebersign .banner img {
    position: relative;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    height: 140px;
}
#memebersign .title {
  height: 40px;
  line-height: 40px;
  font-size: 18px;
  color: #020202;
  font-weight: bolder;
  background: #d3d3d3;
  padding: 0 20px;
}
@media screen and (max-width: 1000px) {
#memebersign .title {
    margin-top: -6px;
}
}
#memebersign .content {
  padding: 30px;
}
#memebersign .content h3 {
    color: #777;
    line-height: 50px;
    font-weight: normal;
    font-size: 16px;
}
#memebersign .content h3 span {
      font-weight: bolder;
}
#memebersign .content label {
    display: block;
    margin: 20px 0;
    margin-bottom: 25px;
}
#memebersign .content label span {
      font-size: 14px;
      font-weight: bolder;
      display: block;
      margin-bottom: 5px;
      color: #000;
}
#memebersign .content label span:before {
        content: '· ';
        font-size: 16px;
        font-weight: bolder;
}
#memebersign .content label input {
      padding: 5px 10px;
}
#memebersign .content label i {
      font-weight: bolder;
      margin-left: 5px;
}
#memebersign .content .payfor {
    position: relative;
    width: 100%;
    max-width: 600px;
}
#memebersign .content .payfor .typeon {
      position: absolute;
      width: 120px;
      height: 40px;
      border: 2px #ccc solid;
      top: -2px;
      left: 0;
      transition: all .5s;
}
#memebersign .content .payfor .typeon img {
        position: absolute;
        width: 30px;
        height: 30px;
        top: 0;
        left: none;
        right: 0px;
}
#memebersign .content .payfor .typeon.show {
        left: 130px;
}
#memebersign .content .payfor img {
      width: 110px;
      height: 35px;
      margin-right: 20px;
      cursor: pointer;
}
#memebersign .content .payfor img:last-child {
        margin-right: 0;
}
@media screen and (max-width: 330px) {
#memebersign .content .payfor img {
        width: 100px;
        height: 30px;
}
#memebersign .content .payfor .typeon {
        width: 100px;
        height: 30px;
}
#memebersign .content .payfor .typeon.show {
          left: 120px;
}
}
#memebersign .content .payfor button {
      margin-left: 150px;
      background: #1eb7fb;
      color: #fff;
      width: 60px;
      font-size: 15px;
      line-height: 25px;
      font-family: "microsoft yahei";
      height: 30px;
      margin-top: 30px;
      display: block;
}
@media screen and (max-width: 1000px) {
#memebersign .content .payfor button {
        margin: auto;
        margin-top: 30px;
}
}
#memebersign .wecat {
  position: fixed;
  z-index: 9001;
  width: 250px;
  height: 100px;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  background: #fff;
}
#memebersign .wecat h3 {
    font-size: 18px;
    font-weight: normal;
    text-align: center;
    line-height: 70px;
    border-bottom: 1px #ccc solid;
}
#memebersign .wecat button {
    line-height: 29px;
    width: 125px;
    display: block;
    float: left;
    cursor: pointer;
    padding: 0;
    border-right: 1px #ccc solid;
}
#memebersign .wecat button:last-child {
      border: 0;
}
#memebersign .wecat button:hover {
      background: #ccc;
}
#memebersign .layer {
  position: fixed;
  background: rgba(0, 0, 0, 0.5);
  z-index: 9000;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}
#memebersign .mint-popup-bottom {
  width: 80%;
  height: 80%;
  max-width: 300px;
  max-height: 600px;
  margin-top: 10%;
  box-sizing: border-box;
  padding: 20px;
}
#memebersign .mint-popup-top {
  width: 80%;
  height: 80%;
  max-width: 300px;
  max-height: 600px;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  box-sizing: border-box;
  padding: 20px;
}
#memebersign .mint-popup-top .container {
    height: 100%;
    width: 100%;
    overflow-y: auto;
}
#memebersign .mint-popup-top .container h4 {
      font-size: 14px;
      margin-top: 10px;
}
#memebersign .mint-popup-top .container p {
      font-size: 13px;
      margin-top: 10px;
      margin-bottom: 20px;
}
#memebersign .mint-popup-top .container button {
      background: #000;
      color: #fff;
      border: 0;
      height: 30px;
      line-height: 30px;
      width: 80px;
      margin: auto;
      display: block;
}
@media screen and (min-width: 1000px) {
#memebersign .banner {
    height: 249px;
}
#memebersign .banner img {
      height: 100%;
}
#memebersign .mint-popup-top {
    max-width: 700px;
    max-height: 350px;
}
#memebersign .title1000 {
    width: 1000px;
    margin: auto;
}
#memebersign .content {
    width: 1000px;
    margin: auto;
}
}



