
#question .banner {
  height: auto;
  overflow: hidden;
}
#question .banner img {
    position: relative;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    height: 140px;
}
#question .title {
  margin-top: 0px;
  height: 40px;
  line-height: 40px;
  font-size: 18px;
  color: #020202;
  font-weight: bolder;
  background: #d3d3d3;
  padding: 0 20px;
}
@media screen and (max-width: 1000px) {
#question .title {
    margin-top: -6px;
}
}
#question .content {
  padding: 20px;
}
#question .content h4 {
    margin: 8px 0;
}
#question .content p {
    font-size: 14px;
    font-family: "microsoft yahei";
    line-height: 23px;
}
#question .content p:nth-child(1) {
      padding: 0;
}
#question .content p span {
      font-weight: bolder;
}
@media screen and (min-width: 1000px) {
#question .title1000 {
    width: 1000px;
    margin: auto;
}
#question .banner {
    height: 249px;
}
#question .banner img {
      width: auto;
      height: 100%;
}
#question .content {
    width: 1000px;
    margin: auto;
    padding-left: 0;
    box-sizing: border-box;
    padding-right: 290px;
}
}
#question .erwei_content1 .img_box {
  float: left;
  margin-right: 50px;
}
#question .erwei_content1 .img_box h4 {
    padding-left: 15px;
}
#question .erwei_content1 .img_box img {
    width: 150px;
}
#question .erwei_content .img_box {
  margin: auto;
  margin-top: 20px;
  display: block;
  text-align: center;
}
#question .erwei_content .img_box h4 {
    padding-left: 15px;
}
#question .erwei_content .img_box img {
    width: 150px;
}



