
#stort .banner {
  height: 80px;
  overflow: hidden;
}
#stort .banner img {
    width: 100%;
}
#stort .title {
  height: 40px;
  line-height: 40px;
  font-size: 18px;
  color: #020202;
  font-weight: bolder;
  background: #d3d3d3;
  padding: 0 20px;
}
#stort .content {
  padding: 40px 20px;
}
#stort .content .shop_box {
    width: 50%;
    box-sizing: border-box;
    padding: 0 20px 20px 20px;
    border-top: 1px #d3d3d3 solid;
}
#stort .content .shop_box:nth-child(2n-1) {
      border-right: 1px #d3d3d3 solid;
}
#stort .content .shop_box:nth-child(2n) {
      border-left: 1px #fff solid;
}
#stort .content .shop_box:nth-child(1) {
      border-top: 0;
}
#stort .content .shop_box:nth-child(2) {
      border-top: 0;
}
#stort .content .shop_box img {
      width: 100%;
      display: block;
}
#stort .content .shop_box h5 {
      margin-top: 20px;
      margin-bottom: 10px;
      font-size: 15px;
}



