
@charset "UTF-8";
#member .banner {
  width: 100%;
  overflow: hidden;
}
#member .banner img {
    position: relative;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    height: 140px;
}
#member .title {
  height: 40px;
  line-height: 40px;
  font-size: 18px;
  color: #020202;
  font-weight: bolder;
  background: #d3d3d3;
  padding: 0 20px;
}
@media screen and (max-width: 1000px) {
#member .title {
    margin-top: -6px;
}
}
#member .content {
  background: #fff;
  padding: 20px;
  /*
 				段落
 			 * */
  /*二维码*/
  /*
 		 * 个人会员计划
 		 */
  /*
 		type1 的下拉
 		 * */
  /*
 		 type2 的下拉
 		 * */
  /*
 			名人堂循环弹窗
 		 * */
}
#member .content .pc_erji {
    display: none;
}
#member .content p {
    font-size: 14px;
    font-family: "microsoft yahei";
    line-height: 23px;
}
#member .content p:nth-child(1) {
      padding: 0;
}
#member .content h4 {
    margin: 8px 0;
}
#member .content img {
    margin: 10px 0;
}
#member .content img:last-child {
    margin: 20px 0;
}
#member .content img:first-child {
    margin: 0;
}
#member .content .contect_box img {
    width: 50%;
    margin: 10px auto;
    margin-top: 20px;
    display: block;
}
#member .content .contect_box h4 {
    font-size: 15px;
    text-align: center;
    color: #bb4545;
}
#member .content .head_2 {
    text-align: center;
}
#member .content .head_2 img {
      width: 28%;
      display: inline-block;
}
#member .content .memeber_img .box_img {
    width: 22%;
    margin: 3%;
    margin-top: 0;
    margin-left: 0;
    float: left;
    box-sizing: border-box;
    border: 5px #f0f0f0 solid;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
}
#member .content .memeber_img .box_img img {
      max-width: 100%;
      max-height: 100%;
}
#member .content .memeber_img .box_img:nth-child(4n) {
      margin-right: 0;
}
#member .content .member_img2 {
    cursor: pointer;
}
#member .content .member_img2 img {
      width: 30%;
      float: left;
      margin: 3%;
      margin-top: 0;
      margin-left: 0;
}
#member .content .member_img2 img:nth-child(3n) {
        margin-right: 0;
}
#member .content .play1 {
    padding: 20px 0;
}
#member .content .play1 .new_box {
      padding: 20px 0;
}
#member .content .play1 .new_box .new_head {
        padding: 15px 0;
        border-bottom: 1px #949393 solid;
}
#member .content .play1 .new_box .new_head span {
          display: block;
          color: #d3be95;
          margin-right: 10px;
          font-size: 25px;
          -webkit-transform: rotate(90deg);
                  transform: rotate(90deg);
          transition: all .5s;
}
#member .content .play1 .new_box .new_head span.tran {
            -webkit-transform: rotate(180deg);
                    transform: rotate(180deg);
}
#member .content .play1 .new_box .new_head h2 {
          width: 80%;
          font-size: 20px;
          color: #949393;
}
#member .content .play1 .new_box .body {
        height: 0;
        opacity: 0;
        overflow: hidden;
        transition: opacity 1.5s;
        transition: padding .3s;
}
#member .content .play1 .new_box .body.show {
          height: auto;
          padding: 20px 0;
          opacity: 1;
}
#member .content .play1 .new_box .body h4 {
          font-size: 16px;
          color: #b39b6a;
}
#member .content .play1 .new_box .body p {
          font-size: 14px;
          font-family: "microsoft yahei";
          line-height: 23px;
          padding: 10px 0;
}
#member .content .play2 .box_head {
    padding: 20px 0;
    border-bottom: 1px #949393 solid;
}
#member .content .play2 .box_head span {
      display: block;
      color: #d3be95;
      margin-right: 10px;
      font-size: 25px;
      transition: all .5s;
      -webkit-transform: rotate(90deg);
              transform: rotate(90deg);
      margin-bottom: 20px;
}
#member .content .play2 .box_head span.tran {
        -webkit-transform: rotate(180deg);
                transform: rotate(180deg);
}
#member .content .play2 .box_head h2 {
      width: 80%;
      color: #949393;
      font-size: 19px;
}
#member .content .play2 .box_main {
    opacity: 0;
    height: 0;
    overflow: hidden;
    transition: opacity 1s;
}
#member .content .play2 .box_main p {
      padding: 10px 0;
      line-height: 23px;
      font-size: 14px;
      color: #3c3c3c;
      font-family: "microsoft yahei";
      font-weight: 400;
}
#member .content .play2 .box_main.show {
      opacity: 1;
      height: auto;
}
#member .content .play3 .img_box {
    display: inline-block;
    width: 45%;
    margin: 2.5%;
    margin-bottom: 0;
}
#member .content .play3 .img_box img {
      width: 100%;
      margin: 0;
}
#member .content .play3 .img_box h4 {
      font-size: 16px;
      color: #333;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
      margin-bottom: 10px;
}
#member .content .play3 .layer {
    width: 100%;
    height: 100%;
    z-index: 99;
    background: rgba(0, 0, 0, 0.5);
    position: fixed;
    top: 0;
    left: 0;
    opacity: 0;
    display: none;
    transition: opacity 1s;
}
#member .content .play3 .layer.show {
      opacity: 1;
      display: block;
}
#member .content .play3 .content_box {
    position: fixed;
    top: 50%;
    left: 50%;
    width: 85%;
    max-width: 327px;
    z-index: 100;
    height: 70%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    background: #000;
    padding: 30px 15px;
    box-sizing: border-box;
}
#member .content .play3 .content_box i {
      position: absolute;
      right: -0px;
      top: -6px;
      color: #fff;
      padding: 10px;
      z-index: 2000;
      font-size: 30px;
}
#member .content .play3 .content_box .over_row {
      overflow-y: auto;
      height: 100%;
      width: 100%;
      color: #fff;
      font-size: 13px;
}
#member .content .play3 .content_box .over_row img {
        width: 45%;
        float: left;
        margin: 0;
        margin-right: 10px;
}
#member .content .play3 .content_box .over_row .jianli {
        min-height: 130px;
        padding-bottom: 10px;
        font-size: 12px;
}
#member .content .play3 .content_box .over_row .jianli h3 {
          font-size: 16px;
          margin-bottom: 5px;
          white-space: nowrap;
          overflow: hidden;
          text-overflow: ellipsis;
}
#member .content .play3 .content_box .over_row .jianli p {
          font-size: 13px;
}
@media screen and (min-width: 1000px) {
#member .title1000 {
    width: 1000px;
    margin: auto;
}
#member .banner {
    height: 249px;
}
#member .banner img {
      height: 100%;
}
#member .content {
    position: relative;
    width: 710px;
    padding-right: 290px;
    margin: auto;
    padding-left: 0;
}
#member .content .pc_erji {
      display: block;
      position: absolute;
      height: auto;
      top: -110px;
      right: 0;
      min-height: 375px;
      min-width: 230px;
}
#member .content .pc_erji .pc_erji_head {
        height: 50px;
        width: 100%;
        background: #4d4d4d;
        font-size: 22px;
        text-align: center;
        margin-bottom: 20px;
        line-height: 50px;
        color: #eaeaea;
}
#member .content .pc_erji .pc_erji_list {
        background: #a4a4a4;
        width: 100%;
        color: #cfcfcf;
        padding: 10px 0;
}
#member .content .pc_erji .pc_erji_list ul {
          padding-left: 0;
}
#member .content .pc_erji .pc_erji_list li {
          height: 40px;
          line-height: 35px;
          margin: 5px 0;
          font-size: 18px;
          cursor: pointer;
          padding-left: 20px;
          padding-right: 10px;
}
#member .content .pc_erji .pc_erji_list li span {
            opacity: 0;
            position: relative;
            top: 10px;
            transition: all .3s;
}
#member .content .pc_erji .pc_erji_list li a {
            color: #cfcfcf;
            transition: all .3s;
}
#member .content .pc_erji .pc_erji_list li:hover {
            color: #fff;
}
#member .content .pc_erji .pc_erji_list li:hover span {
              opacity: 1;
}
#member .content .pc_erji .pc_erji_list li.current {
            color: #fff;
}
#member .content .pc_erji .pc_erji_list li.current span {
              opacity: 1;
}
}



