
#plan .banner {
  height: 80px;
  overflow: hidden;
}
#plan .banner img {
    width: 100%;
}
#plan .title {
  height: 40px;
  line-height: 40px;
  font-size: 18px;
  color: #020202;
  font-weight: bolder;
  background: #d3d3d3;
  padding: 0 20px;
}
#plan .plan_head {
  padding: 30px 0;
}
#plan .plan_head img {
    height: 100px;
    display: block;
    margin: auto;
}
#plan .content {
  padding: 40px 20px;
}
#plan .content h4 {
    line-height: 35px;
    font-size: 17px;
    font-weight: bolder;
}
#plan .content p {
    padding: 10px 0;
    line-height: 23px;
    font-size: 17px;
    color: #3c3c3c;
    font-family: "microsoft yahei";
    font-weight: 400;
}
#plan .content .member {
    width: 100%;
}
#plan .content .member img {
      width: 100%;
}
#plan .content .join_box {
    margin-top: 20px;
    width: 100%;
    border-bottom: 1px #d3d3d3 solid;
}



