
#fame .banner {
  height: 135px;
  overflow: hidden;
}
#fame .banner img {
    width: 100%;
}
#fame .title {
  height: 40px;
  line-height: 40px;
  font-size: 18px;
  color: #020202;
  font-weight: bolder;
  background: #d3d3d3;
  padding: 0 20px;
}
#fame .content {
  padding: 40px 20px;
}
#fame .content h4 {
    line-height: 35px;
    font-size: 18px;
    font-weight: bolder;
}
#fame .content p {
    padding: 10px 0;
    line-height: 23px;
    font-size: 15px;
    color: #3c3c3c;
    font-family: "microsoft yahei";
    font-weight: 400;
}
#fame .content .mian_head {
    margin-top: 30px;
}
#fame .content .mian_head img {
      width: 28%;
      margin-right: 5%;
}
#fame .content .mian_head .head_2 {
      text-align: center;
}
#fame .content .mian_head .head_2 img {
        display: inline-block;
        margin: 2%;
}
#fame .introduce_box {
  width: 100%;
}
#fame .introduce_box .intro_body {
    width: 47%;
    float: left;
    min-height: 220px;
}
#fame .introduce_box .intro_body img {
      display: block;
      width: 100%;
}
#fame .introduce_box .intro_body:nth-child(2n) {
      float: right;
}
#fame .mint-popup-top {
  background: #000;
  height: 70%;
  width: 90%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  overflow: hidden;
  box-sizing: border-box;
  padding: 40px 15px 15px 15px;
}
#fame .mint-popup-top h4 {
    line-height: 35px;
    font-size: 18px;
    font-weight: bolder;
}
#fame .mint-popup-top p {
    padding: 10px 0;
    line-height: 23px;
    font-size: 17px;
    color: #3c3c3c;
    font-family: "microsoft yahei";
    font-weight: 400;
}
#fame .mint-popup-top i {
    position: absolute;
    right: -0px;
    top: -6px;
    color: #fff;
    padding: 10px;
    z-index: 2000;
    font-size: 30px;
}
#fame .mint-popup-top .disc {
    overflow: auto;
    width: 100%;
    height: 100%;
    box-sizing: border-box;
    padding-bottom: 50px;
}
#fame .mint-popup-top .disc img {
      width: 48%;
      box-sizing: border-box;
      float: left;
}
#fame .mint-popup-top .disc .jianli {
      float: left;
      width: 45%;
      padding-left: 15px;
}
#fame .mint-popup-top .disc .jianli p {
        padding: 0;
        font-size: 13px;
}
#fame .mint-popup-top .disc h3 {
      color: #fff;
}
#fame .mint-popup-top .disc p {
      padding: 20px 0px;
      color: #fff;
      font-size: 14px;
      font-weight: normal;
}
#fame .layer_box {
  z-index: 10;
  position: fixed;
  background: rgba(0, 0, 0, 0.5);
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}



