
#home-mine .banner {
  height: auto;
  overflow: hidden;
}
#home-mine .banner img {
    position: relative;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    height: 140px;
}
#home-mine .title {
  height: 40px;
  line-height: 40px;
  font-size: 18px;
  color: #020202;
  font-weight: bolder;
  background: #d3d3d3;
  padding: 0 20px;
}
@media screen and (max-width: 1000px) {
#home-mine .title {
    margin-top: -6px;
}
}
#home-mine .content {
  background: #fff;
  padding: 40px 20px;
}
#home-mine .content .img_box {
    height: 100px;
    width: 100px;
    border: 1px #d3d3d3 solid;
    padding: 3px;
}
#home-mine .content .img_box img {
      width: 100%;
      height: 100%;
}
#home-mine .content .disc {
    margin-left: 20px;
}
#home-mine .content .disc h4 {
      font-size: 16px;
      line-height: 26px;
      font-weight: bloder;
      color: #000;
}
#home-mine .content .disc h5 {
      font-weight: normal;
      font-size: 14px;
      color: #000;
      line-height: 25px;
}
#home-mine .content .disc h5 a {
        font-weight: 600;
        display: inline-block;
        color: #000;
        margin: 0 4px;
}
#home-mine .content .disc h5 a:first-child {
          margin-left: 0;
}
#home-mine .content .mine_list {
    margin-top: 30px;
    margin-bottom: 20px;
    padding-bottom: 30px;
    border-bottom: 1px #d3d3d3 solid;
}
#home-mine .content .mine_list a {
      padding: 5px 15px;
      background: #d3d3d3;
      cursor: pointer;
      font-weight: 600;
      color: #000;
      font-size: 14px;
      margin-right: 26px;
}
#home-mine .content .mine_list a.active {
        color: #c11010;
}
#home-mine .content .content_box {
    margin-bottom: 30px;
}
#home-mine .content .content_box .content_head {
      padding: 20px 0;
}
#home-mine .content .content_box .content_head h1 {
        font-size: 26px;
        color: #949393;
}
#home-mine .content .content_box .content_head p {
        font-size: 12px;
        color: #949393;
}
#home-mine .content .content_body img {
    max-width: 100%;
    margin-bottom: 5px;
}
@media screen and (min-width: 1000px) {
#home-mine .title1000 {
    width: 1000px;
    margin: auto;
}
#home-mine .banner {
    height: 249px;
}
#home-mine .banner img {
      height: 100%;
}
#home-mine .content {
    position: relative;
    width: 710px;
    padding-right: 290px;
    margin: auto;
    padding-left: 0;
}
#home-mine .content .disc {
      width: 202px;
}
}
@media screen and (max-width: 358px) {
#home-mine .title {
    font-size: 16px;
    margin-top: -8px;
    height: 40px;
}
#home-mine .content .img_box {
    width: 90px;
    height: 90px;
}
#home-mine .content .disc {
    margin-left: 10px;
}
#home-mine .content .disc h4 {
      font-size: 14px;
}
#home-mine .content .disc h5 {
      font-size: 12px;
}
#home-mine .content .mine_list {
    margin-top: 13px;
}
#home-mine .content .mine_list a {
      font-size: 13px;
      padding: 5px 4%;
}
}



