
#pcfooter .layer {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  z-index: 4000;
  display: none;
}
#pcfooter .layer.on {
    display: block;
}
#pcfooter .mint-popup-top {
  background: #000;
  top: 50%;
  width: 300px;
  height: 300px;
  margin-top: -150px;
}
#pcfooter .mint-popup-top .close_btn {
    position: absolute;
    right: 0;
    top: 0;
    color: #fff;
    cursor: pointer;
}
#pcfooter .mint-popup-top .imgbox {
    width: 86%;
    margin: auto;
    margin-top: 50px;
}
#pcfooter .mint-popup-top .imgbox img {
      width: 60%;
      margin: auto;
      display: block;
}
#pcfooter .mint-popup-top .imgbox h4 {
      margin-top: 3%;
      text-align: center;
      font-weight: normal;
      font-size: 14px;
      color: #fff;
}
#pcfooter .mint-popup-top .imgbox:last-child {
      margin-top: 50px;
}
#pcfooter .div_erweima {
  position: fixed;
  width: 830px;
  height: 350px;
  top: -50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  background: #000;
  z-index: 5000;
  transition: all .3s;
}
#pcfooter .div_erweima span {
    position: absolute;
    right: 0;
    top: 0;
    color: #fff;
    background: none;
    border: 0;
}
#pcfooter .div_erweima.on {
    top: 50%;
}
#pcfooter .div_erweima .erweima_content img {
    display: block;
    margin: auto;
    margin-top: 60px;
}
#pcfooter .mian_1000px {
  width: 1000px;
  margin: auto;
}
#pcfooter .footer1 {
  padding-left: 0;
}
#pcfooter .footer1 li {
    margin-right: 10px;
    color: #fff;
    width: 140px;
    font-size: 16px;
    float: left;
}
#pcfooter .footer1 li:last-child {
      width: 50px;
}
#pcfooter .footer2 {
  padding-left: 0;
}
#pcfooter .footer2 li {
    margin-right: 10px;
    float: left;
    color: #999;
    font-size: 12px;
    width: 140px;
}
#pcfooter .footer2 li div {
      cursor: pointer;
      margin-top: 10px;
      transition: all .3s;
}
#pcfooter .footer2 li div a {
        color: #999;
        transition: all .3s;
}
#pcfooter .footer2 li div a:hover {
          color: #fff;
}
#pcfooter .footer2 li div:hover {
        color: #fff;
}
#pcfooter .pcfooter_box {
  background: #363533;
  height: 329px;
  width: 100%;
  min-width: 1000px;
}
#pcfooter .pcfooter_box .footer_head {
    width: 100%;
    height: 215px;
    padding-top: 30px;
}
#pcfooter .pcfooter_box .footer_btm {
    border-top: 1px #ccc dotted;
    width: 100%;
    padding-top: 20px;
    color: #999;
    font-size: 12px;
}
#pcfooter .pcfooter_box .footer_btm p {
      display: inline-block;
}
#pcfooter .pcfooter_box .footer_btm h5 {
      display: inline-block;
      margin-left: 20px;
}
#pcfooter .pcfooter_box .footer_btm h5 a {
        color: #999;
}
#pcfooter .pcfooter_box .footer_btm .logo_box img {
      margin-right: 20px;
      width: 85px;
}
#pcfooter .pcfooter_box .footer_btm .logo_box img:nth-child(2) {
        margin-top: 5px;
}



