
#cy_summit .banner {
  height: 80px;
  overflow: hidden;
}
#cy_summit .banner img {
    width: 100%;
}
#cy_summit .title {
  height: 40px;
  line-height: 40px;
  font-size: 18px;
  color: #020202;
  font-weight: bolder;
  background: #d3d3d3;
  padding: 0 20px;
}
#cy_summit .content {
  padding: 40px 20px;
}
#cy_summit p {
  padding: 10px 0;
  line-height: 23px;
  font-size: 15px;
  color: #3c3c3c;
  font-family: "microsoft yahei";
  font-weight: 400;
}



