
#query .banner {
  height: auto;
  overflow: hidden;
}
#query .banner img {
    position: relative;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    height: 140px;
}
@media screen and (max-width: 800px) {
#query .banner img {
        left: 16%;
}
}
#query .title {
  height: 40px;
  line-height: 40px;
  font-size: 18px;
  color: #020202;
  font-weight: bolder;
  background: #d3d3d3;
  padding: 0 20px;
}
@media screen and (max-width: 1000px) {
#query .title {
    margin-top: -6px;
}
}
#query .content {
  padding: 20px;
}
#query .content form label {
    display: block;
    padding: 20px 0;
}
#query .content form label input {
      padding: 3px 10px;
}
#query .content form label span {
      display: block;
      margin-bottom: 10px;
      font-family: "din", "microsoft yahei", sans-serif !important;
}
#query .content form label i {
      color: #F60;
      font-style: normal;
}
@media screen and (max-width: 600px) {
#query .content form label i {
        display: block;
        margin-top: 10px;
}
}
#query .content form button {
    width: 80px;
    margin-top: 20px;
    font-family: "din","microsoft yahei";
    font-size: 15px;
    background: #000;
    color: #fff;
    padding: 3px 20px;
}
#query .content table {
    margin-top: 20px;
    width: 100%;
}
#query .content table td {
      padding: 10px 0px;
      box-sizing: border-box;
      font-family: "microsoft yahei","din";
}
@media screen and (max-width: 500px) {
#query .content table td {
        font-size: 12px;
}
}
#query .content img.ali_pay {
    margin-top: 20px;
    margin-bottom: 50px;
    border: 1px #ccc solid;
    cursor: pointer;
}
#query .pc_erji {
  display: none;
}
#query .pc_picture {
  position: relative;
  overflow: hidden;
  transition: all .5s;
  cursor: pointer;
}
#query .pc_picture i {
    font-style: normal;
}
#query .pc_picture button {
    position: absolute;
    transition: all .5s;
    z-index: 500;
    color: #fff;
    top: 50%;
    left: 0;
    background: rgba(0, 0, 0, 0.5);
    padding: 10px;
    -webkit-transform: translateY(-60%);
            transform: translateY(-60%);
}
#query .pc_picture button i {
      font-size: 40px;
}
#query .pc_picture button:nth-child(2) {
      left: auto;
      right: -60px;
}
#query .pc_picture button:nth-child(1) {
      left: -60px;
      right: auto;
}
@media screen and (min-width: 1000px) {
#query .title1000 {
    width: 1000px;
    margin: auto;
}
#query .banner {
    height: 249px;
}
#query .banner img {
      height: 100%;
}
#query .content {
    width: 1000px;
    margin: auto;
    box-sizing: border-box;
    padding-right: 290px;
    position: relative;
}
#query .content .pc_erji {
      display: block;
      position: absolute;
      height: auto;
      top: -110px;
      right: 0;
      min-height: 375px;
      min-width: 230px;
}
#query .content .pc_erji .pc_erji_head {
        height: 50px;
        width: 100%;
        background: #4d4d4d;
        font-size: 22px;
        text-align: center;
        margin-bottom: 20px;
        line-height: 50px;
        color: #eaeaea;
}
#query .content .pc_erji .pc_erji_list {
        background: #a4a4a4;
        width: 100%;
        color: #cfcfcf;
        padding: 10px 0;
}
#query .content .pc_erji .pc_erji_list ul {
          padding-left: 0;
}
#query .content .pc_erji .pc_erji_list li {
          height: 40px;
          line-height: 35px;
          margin: 5px 0;
          font-size: 18px;
          cursor: pointer;
          padding-left: 20px;
          padding-right: 10px;
}
#query .content .pc_erji .pc_erji_list li span {
            opacity: 0;
            position: relative;
            top: 10px;
            transition: all .3s;
}
#query .content .pc_erji .pc_erji_list li a {
            color: #cfcfcf;
            transition: all .3s;
}
#query .content .pc_erji .pc_erji_list li:hover a {
            color: #fff;
}
#query .content .pc_erji .pc_erji_list li:hover span {
            opacity: 1;
}
#query .content .pc_erji .pc_erji_list li.current span {
            opacity: 1;
}
#query .content .pc_erji .pc_erji_list li.current a {
            color: #fff;
}
}



