
#awards .banner {
  height: 95px;
  overflow: hidden;
}
#awards .title {
  height: 40px;
  line-height: 40px;
  font-size: 18px;
  color: #020202;
  font-weight: bolder;
  background: #d3d3d3;
  padding: 0 20px;
}
#awards .content {
  padding: 40px 20px;
}
#awards .content h4 {
    line-height: 35px;
    font-size: 17px;
    font-weight: bolder;
}
#awards .content p {
    padding: 10px 0;
    line-height: 23px;
    font-size: 15px;
    color: #3c3c3c;
    font-family: "microsoft yahei";
    font-weight: 400;
}
#awards .content p a {
      color: #000;
}
#awards img.bottom {
  margin-bottom: 10px;
}



