
#listEnter .banner {
  height: auto;
  overflow: hidden;
}
#listEnter .banner img {
    position: relative;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    height: 140px;
}
@media screen and (max-width: 800px) {
#listEnter .banner img {
        left: 16%;
}
}
#listEnter .title {
  height: 40px;
  line-height: 40px;
  font-size: 18px;
  color: #020202;
  font-weight: bolder;
  background: #d3d3d3;
  padding: 0 20px;
}
#listEnter table {
  border: 1px solid #999;
  color: #777;
  font-size: 14px;
  font-family: "din", "microsoft yahei" !important;
  width: 100%;
  border-collapse: collapse;
}
#listEnter table tr td {
    padding: 5px;
    box-sizing: border-box;
}
#listEnter table p {
    color: #777;
}
@media screen and (max-width: 1000px) {
#listEnter table {
    font-size: 12px;
}
#listEnter table p {
      font-size: 12px;
}
}
@media screen and (max-width: 1000px) {
#listEnter .title {
    margin-top: -5px;
}
}
#listEnter .content {
  padding: 20px;
}
#listEnter p {
  padding: 10px 0;
  line-height: 23px;
  font-size: 15px;
  color: #3c3c3c;
  font-family: "microsoft yahei";
  font-weight: 400;
}
#listEnter p:first-child {
    padding-top: 0;
}
@media screen and (min-width: 1000px) {
#listEnter .title1000 {
    width: 1000px;
    margin: auto;
}
#listEnter .banner {
    height: 249px;
}
#listEnter .banner img {
      height: 100%;
}
#listEnter .content {
    width: 1000px;
    margin: auto;
    padding-right: 300px;
    box-sizing: border-box;
}
}



