
#contact .banner {
  height: 80px;
  overflow: hidden;
}
#contact .banner img {
    width: 100%;
}
#contact .title {
  height: 40px;
  line-height: 40px;
  font-size: 18px;
  color: #020202;
  font-weight: bolder;
  background: #d3d3d3;
  padding: 0 20px;
}
#contact .content {
  padding: 40px 20px;
}
#contact .content p {
    padding: 10px 0;
    line-height: 23px;
    font-size: 17px;
    color: #3c3c3c;
    font-family: "microsoft yahei";
    font-weight: 400;
}
#contact .content span {
    padding: 0 16px;
    display: inline-block;
}



