
#camp .mint-swipe {
  height: 150px;
}
#camp .mint-swipe img {
    width: 100%;
}
#camp .mint-swipe .mint-swipe-indicator {
    opacity: 0.4;
}
#camp .mint-swipe .mint-swipe-indicator.is-active {
    opacity: 1;
}
#camp .mint-swipe .mint-swipe-indicators {
    bottom: 3px;
}
#camp .title {
  height: 40px;
  line-height: 40px;
  font-size: 18px;
  color: #020202;
  font-weight: bolder;
  background: #d3d3d3;
  padding: 0 20px;
}
#camp .content {
  padding: 40px 20px;
}
#camp .content h4 {
    line-height: 35px;
    font-size: 16px;
    font-weight: bolder;
}
#camp .content p {
    padding: 10px 0;
    line-height: 23px;
    font-size: 15px;
    color: #3c3c3c;
    font-family: "microsoft yahei";
    font-weight: 400;
}
#camp .content p span {
      font-weight: bolder;
}
#camp .content h3 {
    margin-bottom: 20px;
}
#camp .content .activety_box {
    padding: 10px 0;
}



