
#ambassadorMore .banner {
  height: auto;
  overflow: hidden;
}
#ambassadorMore .banner img {
    position: relative;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    height: 140px;
}
#ambassadorMore .title {
  height: 40px;
  line-height: 40px;
  font-size: 18px;
  color: #020202;
  font-weight: bolder;
  background: #d3d3d3;
  padding: 0 20px;
}
@media screen and (max-width: 1000px) {
#ambassadorMore .title {
    margin-top: -6px;
}
}
#ambassadorMore .content {
  padding: 40px 20px;
}
#ambassadorMore .content .page_list {
    padding-left: 0;
    padding: 10px 0;
    margin-top: 50px;
    text-align: center;
    border: 1px #333 solid;
    border-width: 1px 0;
}
#ambassadorMore .content .page_list li {
      cursor: pointer;
      display: inline-block;
      padding: 5px 10px;
      font-size: 14px;
}
#ambassadorMore .content .page_list li.active {
        background: #BCBCBC;
}
#ambassadorMore .content .page_list span {
      font-size: 12px;
      padding: 0 2px;
}
#ambassadorMore .content .ambassador_list {
    padding-left: 0;
}
#ambassadorMore .content .ambassador_list li {
      background: #d3d3d3;
      width: 31%;
      height: 160px;
      box-sizing: border-box;
      padding: 10px 15px;
      margin-right: 3%;
      float: left;
      margin-top: 15px;
}
#ambassadorMore .content .ambassador_list li:nth-child(3n) {
        margin-right: 0;
}
#ambassadorMore .content .ambassador_list li .img_box {
        height: 98.453px;
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -ms-flex-line-pack: center;
            align-content: center;
        -webkit-box-orient: vertical;
        -webkit-box-direction: normal;
            -ms-flex-direction: column;
                flex-direction: column;
        -webkit-box-pack: center;
            -ms-flex-pack: center;
                justify-content: center;
        margin-bottom: 3px;
}
#ambassadorMore .content .ambassador_list li img {
        max-height: 100%;
        max-width: 100%;
        width: auto;
        margin: 0;
}
#ambassadorMore .content .ambassador_list li h3 {
        font-size: 12px;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
}
#ambassadorMore .content .ambassador_list li h1 {
        font-size: 12px;
        width: 50px;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        position: relative;
}
#ambassadorMore .content .ambassador_list li .box {
        position: relative;
        height: 15px;
}
#ambassadorMore .content .ambassador_list li .box .diamond {
          position: absolute;
          top: 0;
          right: -8px;
          min-width: 10px;
          padding: 0 3px;
          background: #000;
          height: 15px;
          color: #fff;
          font-size: 12px;
}
#ambassadorMore .content .ambassador_list li .box .diamond .left {
            height: 15px;
            width: 10px;
            position: absolute;
            left: -8px;
            top: 0;
            overflow: hidden;
}
#ambassadorMore .content .ambassador_list li .box .diamond .left .sanjiao {
              position: absolute;
              top: 2px;
              left: 2px;
              background: #000;
              width: 11px;
              height: 11px;
              -webkit-transform: rotate(45deg);
                      transform: rotate(45deg);
}
#ambassadorMore .content .ambassador_list li .box .diamond .right {
            height: 15px;
            width: 8px;
            position: absolute;
            right: -7px;
            top: 0;
            overflow: hidden;
}
#ambassadorMore .content .ambassador_list li .box .diamond .right .sanjiao {
              position: absolute;
              top: 2px;
              right: 2px;
              background: #000;
              width: 11px;
              height: 11px;
              -webkit-transform: rotate(45deg);
                      transform: rotate(45deg);
}
@media screen and (min-width: 1000px) {
#ambassadorMore .title1000 {
    width: 1000px;
    margin: auto;
    box-sizing: border-box;
    padding-right: 290px;
}
#ambassadorMore .banner {
    height: 249px;
}
#ambassadorMore .banner img {
      height: 100%;
}
#ambassadorMore .content {
    position: relative;
    width: 1000px;
    padding-right: 290px;
    box-sizing: border-box;
    padding-left: 0;
    margin: auto;
}
#ambassadorMore .content .ambassador_list li {
      width: 14.1%;
}
#ambassadorMore .content .ambassador_list li:nth-child(3n) {
        margin-right: 3%;
}
#ambassadorMore .content .ambassador_list li:nth-child(6n) {
        margin-right: 0;
}
#ambassadorMore .content .page_list {
      cursor: pointer;
}
}



