
.mint-toast {
  z-index: 9999;
}
.toast {
  z-index: 9999;
}
#pclogin .toast {
  z-index: 9999;
}
#pclogin input {
  border: 1px;
}
#pclogin p {
  color: #fff;
}
#pclogin .layer {
  background: rgba(0, 0, 0, 0.8);
  z-index: 1999;
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  display: none;
}
#pclogin .layer.layer_show {
    display: block;
}
#pclogin .find_box {
  position: fixed;
  left: 50%;
  width: 530px;
  height: 200px;
  margin-top: -100px;
  padding: 20px;
  margin-left: -265px;
  background: #000;
  z-index: 2000;
  top: -50%;
  transition: all .5s;
}
#pclogin .find_box h1 {
    color: #fff;
    font-size: 25px;
    font-weight: 500;
}
#pclogin .find_box p {
    font-size: 14px;
    margin-top: 20px;
}
#pclogin .find_box label {
    padding: 20px 0;
    display: block;
}
#pclogin .find_box label span {
      color: #fff;
      font-family: "microsoft yahei";
      font-size: 14px;
}
#pclogin .find_box label input {
      padding: 5px 10px;
      margin-left: 10px;
}
#pclogin .find_box button {
    display: block;
    font-size: 14px;
    font-family: "din",'micrsoft yahei';
    margin-top: 20px;
    border: 1px #fff solid;
    padding: 3px 15px;
    color: #fff;
}
#pclogin .find_box.find_show {
    top: 50%;
}
#pclogin .main_box {
  height: 360px;
  width: 1000px;
  position: fixed;
  top: 50%;
  margin-top: -180px;
  left: 50%;
  margin-left: -500px;
  z-index: 999;
  background: url("/static/img/sign_bg.png");
  background-repeat: repeat-y;
}
#pclogin .main_box .close_btn {
    position: absolute;
    right: 0;
    top: 0;
    color: #fff;
    background: none;
    border: 0;
}
#pclogin .main_box .pclogin_l {
    padding: 35px 70px;
    box-sizing: border-box;
    width: 420px;
    height: 100%;
}
#pclogin .main_box .pclogin_l h1 {
      font-size: 36px;
      line-height: 40px;
      color: #fff;
}
#pclogin .main_box .pclogin_l .title {
      width: 420px;
      margin-top: 60px;
}
#pclogin .main_box .pclogin_l .title .tit {
        font-size: 18px;
        color: #fff;
        margin-top: 15px;
        margin-left: 20px;
}
#pclogin .main_box .pclogin_l button.btn {
      display: block;
      width: 280px;
      height: 40px;
      margin-top: 60px;
      line-height: 40px;
      color: #05A8EC;
      font-size: 16px;
      text-align: center;
      background: #fff;
      letter-spacing: 5px;
      cursor: pointer;
}
#pclogin .main_box .pclogin_r {
    padding: 35px 70px;
    padding-top: 85px;
    box-sizing: border-box;
    width: 575px;
    height: 100%;
}
#pclogin .main_box .pclogin_r .findpass {
      margin-left: 90px;
      margins-top: 10px;
      display: block;
      color: #fff;
}
#pclogin .main_box .pclogin_r label {
      display: block;
      margin-bottom: 20px;
      height: 35px;
}
#pclogin .main_box .pclogin_r label span {
        display: inline-block;
        width: 65px;
        line-height: 35px;
        font-size: 16px;
        color: #fff;
        margin-right: 20px;
}
#pclogin .main_box .pclogin_r label input {
        background: #ABEDFE;
        padding: 10px;
        width: 60%;
}
#pclogin .main_box .pclogin_r a {
      display: block;
      margin-top: 10px;
      cursor: pointer;
}
#pclogin .main_box .pclogin_r button {
      display: block;
      width: 280px;
      height: 40px;
      margin-top: 60px;
      line-height: 40px;
      color: #05A8EC;
      font-size: 16px;
      text-align: center;
      background: #fff;
      letter-spacing: 5px;
      cursor: pointer;
      margin-left: 90px;
}
#pclogin .main_box .pclogin_r:last-child {
      padding-top: 60px;
}
#pclogin .main_box .pclogin_r:last-child label {
        display: block;
        margin-bottom: 10px;
        height: 35px;
}
#pclogin .main_box .pclogin_r:last-child label span {
          line-height: 35px;
          font-size: 16px;
          color: #fff;
          margin-right: 20px;
}
#pclogin .main_box .pclogin_r:last-child label input {
          background: #ABEDFE;
          padding: 5px 10px;
          width: 60%;
}
#pclogin .main_box .pclogin_r:last-child label input.code_input {
            width: 65px;
}
#pclogin .main_box .pclogin_r:last-child label .code {
          display: inline-block;
}
#pclogin .main_box .pclogin_r:last-child label .code img {
            position: relative;
            top: 5px;
}
#pclogin .main_box .pclogin_r:last-child label .code i {
            font-style: normal;
            color: #fff;
            cursor: pointer;
}
#pclogin .main_box .pclogin_r:last-child button {
        margin-top: 20px;
}
#pclogin .main_box .center {
    float: left;
    height: 100%;
    width: 3px;
    background: url("/static/img/sign_bg2.png");
}



